export default {
	data() {
		return {
			region: ["广东省", "深圳市", "龙岗区"],
			name: '',//收货人
			phone: '',//电话
			address: '',//详细地址
			index: 0,//地址id
			default1: 1,//是否默认
			zip: "",//邮编
			type:''//修改还是新增
		}
	},
	methods: {
		//返回上一层
		warp() {
			uni.navigateBack({
				delta: 1
			})
		},
		//改变地址
		changeRegin(e) {
			this.region = e.target.value
		},
		//删除收货地址
		del() {
			let that = this
			uni.showModal({
				title: '提示',
				content: '是否确定删除？',
				success: function(res) {
					if (res.confirm) {
						uni.request({
							url: "http://ceshi3.dishait.cn/api/useraddresses/"+that.index+"",
							header: {
								"token": "a4c30e2a39426b875290fcbd91c16727b2b7e3c5"
							},
							method: "DELETE",
							success(res) {
								uni.navigateTo({
									url: '../place/Place?'
								})
							}
						})
					} else if (res.cancel) {
						alert('用户点击取消');
					}
				}
			});
		},
		//修改或新增收货地址
		save() {
			let that = this
			console.log(that.type)
			if(that.type=="change"&&that.region&&that.name&&that.phone&&that.address){
				uni.request({
					url: "http://ceshi3.dishait.cn/api/useraddresses/"+that.index+"",
					header: {
						"token": "a4c30e2a39426b875290fcbd91c16727b2b7e3c5"
					},
					method: "POST",
					data: {
						"province": that.region[0],
						"city": that.region[1],
						"district": that.region[2],
						"name": that.name,
						"phone": that.phone,
						'address': that.address,
						"default": that.default1,
						"zip": that.zip
					},
					success(res) {
						console.log(res)
						if (res.data.errorCode) {
							alert("信息填写不符合标准")
						} else {
							uni.navigateTo({
								url: '../place/Place'
							})
						}
					}
				})
			}else if(that.region&&that.name&&that.phone&&that.address){
				uni.request({
					url:'http://ceshi3.dishait.cn/api/useraddresses',
					header:{
						"token": "a4c30e2a39426b875290fcbd91c16727b2b7e3c5"
					},
					method:"POST",
					data: {
						"province": that.region[0],
						"city": that.region[1],
						"district": that.region[2],
						"name": that.name,
						"phone": that.phone,
						'address': that.address,
						"default": that.default1,
						"zip": that.zip
					},
					success(res){
						uni.navigateTo({
							url: '../place/Place'
						})
					}
				})
			}
		},
		//改变是否默认
		turn() {
			if (this.default1 == 0) {
				this.default1 = 1
			} else {
				this.default1 = 0
			}
		}
	},
	onLoad(option) {
		uni.showLoading({
			title: '加载中'
		});
		//初始化数据
		setTimeout(function() {
			uni.hideLoading();
		}, 500);
		let list = JSON.parse(option.name)
		this.region = [list.province, list.city, list.district]
		this.phone = list.phone
		this.name = list.name
		this.index = list.id
		this.type = option.type
	}
}
